import React from 'react';
import ReactDOM from 'react-dom';


class Items extends React.Component{
    constructor(props){
        super(props)
        this.state={pl:{},mn:{}}
        this.props.pnames.forEach(element => {
            this.state.pl[element]=0
        });

        this.props.mnames.forEach(element => {
            this.state.mn[element]=0
        });

    }

    plus(k,v){
        var pls = this.state.pl
        pls[k] = parseInt(v)
        this.setState({pl:pls})

    }

    minus(k,v){
        var mns = this.state.mn
        mns[k] = parseInt(v)
        this.setState({mn:mns})

    }

    render(){
        return(
            <div className="form-group">
 
            {
                this.props.pnames.map( name =>
                    
                    <label className="form-inline" >{ name }
                    <input type="number" className="form-control"  onChange={(e)=> this.plus(name,e.target.value)}></input></label>
                    
                )
            }
            {
                this.props.mnames.map( name =>
                    <label classNam="form-inline">{ name }
                    <input type="number" className="form-control"  onChange={(e)=> this.minus(name,e.target.value)}></input></label>
                )
            }

                <p>
                    加算: {Object.values(this.state.pl).reduce((p,c)=>p+c)} 円   減算: {Object.values(this.state.mn).reduce((p,c)=>p+c)} 円   差し引き: {Object.values(this.state.pl).reduce((p,c)=>p+c)-Object.values(this.state.mn).reduce((p,c)=>p+c)} 円
                </p>

            </div>
        )


    }



}

ReactDOM.render(
    <div>
    <Items pnames={["現金売上","ユニー商品券","その他金券","クレジットカード","クレジットギフト","ユニコ"]} mnames={["消費税","売上記録高"]} />
    </div>
    ,
    document.getElementById('root')
  );
  